* {
    box-sizing: border-box;
  }
  body {
    background: #e4e4e4;
    padding: 5px;
    height: 100vh;
    margin: 0;
  
    font: 500 100% system-ui, sans-serif;
  }
  .wrap {
    background: white;
    height: calc(100vh - 10px);
    box-shadow: 0 0 3px rgba(black, 0.33);
  
    display: grid;
    grid-template-columns: minmax(10px, 1fr) minmax(10px, 3fr);
    grid-template-rows: min-content min-content 1fr min-content;
    gap: 1px;
  
    > * {
      padding: 1rem;
      text-align: center;
    }
  
    @media (max-width: 600px) {
      grid-template-columns: 100%;
      grid-template-rows: auto;
      > * {
        grid-column: 1 / -1 !important;
        grid-row: auto !important;
      }
    }
  }
  
  .header {
    grid-column: 1 / 4;
    background: #ffcdd2;
  }
  
  .sidebar {
    grid-column: 3 / 3;
    grid-row: 2 / 4;
    background: #e1bee7;
  }
  .nav {
    grid-column: 1 / 3;
    background: #bbdefb;
  }
  .nav-list {
    width: 100%;
    margin: 0;
    padding: 0;
  }
  .nav-link {
    list-style-type: none;
    display: inline-block;
    padding-right: 10%;
    width: auto;
    margin-right: 0.5%;
    padding-left: 0;
    margin-left: 0;
  }
  li:last-child {
    margin-right: 0;
    padding-right: 0;
  }

  .main {
    grid-column: 1 / 3;
    background: #dcedc8;
  }
  .footer {
    grid-column: 1 / 4;
    background: #ffecb3;
  }
  
